*
{
    margin: 0;
    padding: 0;
}

html,
body
{
    height: 100vh;
    font-family: 'Roboto','Helvetica','Arial','sans-serif';
}

.webgl
{
    position: fixed;
    top: 0;
    left: 0;
    outline: none;
}

.container
{
    position: absolute;
    z-index: 1;
    width: 100%;    
    height: 100vh;
    display: grid;  
    place-content: center;
}